import React, { useEffect, useState } from "react";
import Avatar from "@material-ui/core/Avatar";

import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline";
import { Button, Input } from "@material-ui/core";
import SendIcon from "@material-ui/icons/Send";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";

import { db } from "../Firebase/Firebase";
import firebase from "firebase";

import { useSelector } from "react-redux";

function Post(props) {
   const { currentUser } = useSelector((state) => state);

   const [comments, setComments] = useState([]);
   const [cmntCount, setCmntCount] = useState(0);
   const [cmnt, setCmnt] = useState("");
   const [currentUsername, setCurrentUsername] = useState("");

   useEffect(() => {
      setCurrentUsername(currentUser.username);
   }, [currentUser]);

   useEffect(() => {
      if (props.postID) {
         db.collection("posts")
            .doc(props.postID)
            .collection("comments")
            .orderBy("timeStamp", "asc")
            .onSnapshot((snap) => {
               setComments(
                  snap.docs.map((doc, index) => {
                     return { id: doc.id, text: doc.data() };
                  })
               );
            });
      }
   }, [props.postID]);
   /* if any variable used inside useEffect then it's better to use them inside this array. */

   const postCmnt = (e) => {
      e.preventDefault();

      db.collection("posts").doc(props.postID).collection("comments").add({
         username: currentUsername,
         comment: cmnt,
         timeStamp: firebase.firestore.FieldValue.serverTimestamp(),
      });

      setCmnt("");
   };

   useEffect(() => {
      setCmntCount(comments.length);
   }, [comments]);

   return (
      <div className="post">
         <div className="post__header p-2">
            <span className="header__avatar">
               <Avatar
                  type="button"
                  alt={props.username}
                  src="/static/images/avatar/1.jpg"
               />
            </span>
            <strong className="header__username">{props.username}</strong>
         </div>
         <div className="post__image">
            {props.imgUrl ? (
               <img
                  src={props.imgUrl}
                  alt="Failed to load"
                  className="image img-fluid"
               />
            ) : (
               <div className="image__errorMsg">
                  <p>
                     <ErrorOutlineIcon className="image__errorMsg-icon" />
                     Failed to load Image <br /> Please check your connection
                  </p>
               </div>
            )}
         </div>
         <div className="post__caption p-2">
            <strong className="caption__username">@{props.username}</strong>
            <span className="caption__text">{props.caption}</span>
         </div>
         <div className="post__show-comments">
            <div className="show-comments__header">
               <strong>{cmntCount} Comments</strong>
            </div>
            {cmntCount ? (
               <div className="show-comments__allComments-wrapper">
                  {comments.map((comm) => {
                     return (
                        <div
                           key={comm.id}
                           className="show-comments__single-comment"
                        >
                           <strong>{comm.text.username} </strong>
                           <br />
                           <span>{comm.text.comment}</span>
                        </div>
                     );
                  })}
               </div>
            ) : (
               <div className="show-comments__emptyMsg">
                  <p>
                     <HighlightOffIcon /> No Comments
                  </p>
               </div>
            )}
         </div>
         <div className="post__commentBox">
            {currentUser.loggedIn ? (
               <form className="commentBox__form">
                  <Input
                     className="commentBox__input"
                     onChange={(e) => setCmnt(e.target.value)}
                     value={cmnt}
                     disableUnderline={true}
                     placeholder="Write a comment"
                  />
                  <Button
                     type="submit"
                     className="commentBox__button"
                     color="primary"
                     disabled={!cmnt}
                     onClick={postCmnt}
                  >
                     <SendIcon className="commentBox__button-icon" />
                  </Button>
               </form>
            ) : (
               <div className="text-center">
                  <p>Please login to comment on this post</p>
               </div>
            )}
         </div>
      </div>
   );
}

export default Post;
