import { combineReducers } from "redux";

const initialObj = {
   username: "unknown",
   loggedIn: false,
};
const currentUser = (state = initialObj, action) => {
   if (action.type === "SET_CURRENT_USER") {
      if (action.username) {
         /* NOTE: if your value has boolean, it's better not to use them here */
         return (state = {
            username: action.username,
            loggedIn: action.isLoggedIn,
         });
      } else {
         return state;
      }
   } else {
      return state;
   }
};

export default combineReducers({ currentUser });
