import React, { useRef, useState, useEffect } from "react";

import { Input, Button, TextareaAutosize } from "@material-ui/core";
import ExpandLessRoundedIcon from "@material-ui/icons/ExpandLessRounded";
import ExpandMoreRoundedIcon from "@material-ui/icons/ExpandMoreRounded";

import { db, storage } from "../Firebase/Firebase";
import firebase from "firebase";

import { useSelector } from "react-redux";

function ImgUpload() {
   const { currentUser } = useSelector((state) => state);

   const [caption, setCaption] = useState("");
   const [file, setFile] = useState(null);
   const [progress, setProgress] = useState(0);

   const [openImgUpload, setOpenImgUpload] = useState(true);
   const [currentUsername, setCurrentUsername] = useState("");

   const file_and_caption = useRef(null);

   useEffect(() => {
      if (currentUser) {
         setCurrentUsername(currentUser.username);
      }
   }, [currentUser]);

   const handleFile = (e) => {
      if (e.target.files[0]) {
         setFile(e.target.files[0]);
      }
   };

   const handleUpload = (e) => {
      e.preventDefault();
      const uploading = storage.ref(`files/${file.name}`).put(file);
      uploading.on(
         "state_changed",
         (snap) => {
            const prog = Math.round(
               (snap.bytesTransferred / snap.totalBytes) * 100
            );
            setProgress(prog);
         },
         (err) => {
            console.log(err);
            alert(err.message);
         },
         () => {
            storage
               .ref("files")
               .child(file.name)
               .getDownloadURL()
               .then((downlaodURL) => {
                  db.collection("posts").add({
                     caption: caption,
                     imgUrl: downlaodURL,
                     username: currentUsername,
                     timeStamp: firebase.firestore.FieldValue.serverTimestamp(),
                  });
               });
            /* storing the file to the firebase [Storage] and getting a [Download Url] and then adding the url to the [Firebase db collection] */

            setProgress(0);
            setCaption("");
            setFile(null);
         }
      );
   };

   const handleIconClick = () => {
      if (openImgUpload) {
         setOpenImgUpload(false);
         file_and_caption.current.style.display = "none";
      } else {
         setOpenImgUpload(true);
         file_and_caption.current.style.display = "initial";
      }
   };

   return progress > 0 ? (
      <div className="imgUpload text-center bg-white">
         <div className="imgUpload__progressBar">
            <progress value={progress} max="100" />
            <span className="parcentage ml-2">{progress}%</span>
         </div>
         <div className="imgUpload__actOnUpload low_visibility">
            <div className="imgUpload__choose-file" onChange={handleFile}>
               <Input fullWidth={true} type="file" disabled />
            </div>
            <div className="imgUpload__caption my-4">
               <TextareaAutosize
                  className="caption__inputBox"
                  rowsMax={4}
                  aria-label="maximum height"
                  placeholder="Write a Caption"
                  onChange={(e) => setCaption(e.target.value)}
                  value={caption}
                  disabled
               />
               <Button
                  className="caption__button"
                  variant="contained"
                  onClick={handleUpload}
                  disabled
               >
                  Upload
               </Button>
            </div>
         </div>
         <p className="owener_name text-center m-0">
            Created By <strong>MD Omar</strong>
         </p>
      </div>
   ) : (
      <div className="imgUpload text-center bg-white">
         <div
            className="imgUpload__upIcon rounded-pill"
            onClick={handleIconClick}
         >
            {openImgUpload ? (
               <ExpandMoreRoundedIcon />
            ) : (
               <ExpandLessRoundedIcon />
            )}
         </div>
         <div ref={file_and_caption} className="actOnIconClick">
            <div className="imgUpload__choose-file" onChange={handleFile}>
               <Input fullWidth={true} type="file" />
            </div>
            <div className="imgUpload__caption my-4">
               <TextareaAutosize
                  className="caption__inputBox"
                  rowsMax={4}
                  aria-label="maximum height"
                  placeholder="Write a Caption"
                  onChange={(e) => setCaption(e.target.value)}
                  value={caption}
               />
               <Button
                  className="caption__button"
                  variant="contained"
                  onClick={handleUpload}
               >
                  Upload
               </Button>
            </div>
         </div>
         <p className="owener_name text-center m-0">
            Created By <strong>MD Omar</strong>
         </p>
      </div>
   );
}

export default ImgUpload;
