import React, { useState, useEffect } from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Modal, Input, Button, InputAdornment } from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";
import EmailIcon from "@material-ui/icons/Email";
import VpnKeyIcon from "@material-ui/icons/VpnKey";
import ExitToAppOutlinedIcon from "@material-ui/icons/ExitToAppOutlined";

import { auth } from "../Firebase/Firebase";

import { useSelector, useDispatch } from "react-redux";
import { setCurrentUser } from "../redux/actions";

function getModalStyle() {
   const top = 50;
   const left = 50;

   return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
   };
}

const useStyles = makeStyles((theme) => ({
   paper: {
      position: "absolute",
      width: 400,
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
   },
}));

export default function SimpleModal(props) {
   const { currentUser } = useSelector((state) => state);
   const dispatch = useDispatch();

   const classes = useStyles();
   // getModalStyle is not a pure function, we roll the style only on the first render
   const [modalStyle] = React.useState(getModalStyle);
   const [openSignup, setOpenSignup] = React.useState(false);
   const [openLogin, setOpenLogin] = useState(false);

   const [user, setUser] = useState("");
   const [email, setEmail] = useState("");
   const [pass, setPass] = useState("");

   const [successMsg, setSuccessMsg] = useState("");

   useEffect(() => {
      const log_inOut = auth.onAuthStateChanged((auth_user) => {
         /* it collects the Session/Cookies of a user -> to get if the user is logged in or out */
         if (auth_user) {
            // if the user logged in
            dispatch(
               setCurrentUser({
                  username: auth_user.displayName,
                  setLoggedIn: true,
               })
            ); // collect all the info about user
         } else {
            dispatch(
               setCurrentUser({
                  username: "unknown",
                  setLoggedIn: false,
               })
            );
         }
      });

      return () => {
         // perform some cleanup actions
         log_inOut();
      };
   }, []);

   const Signup = (e) => {
      e.preventDefault();

      auth
         .createUserWithEmailAndPassword(email, pass)
         .then((auth_user) => {
            auth_user.user.updateProfile({
               displayName: user,
            });
            dispatch(
               setCurrentUser({
                  username: user,
                  setLoggedIn: true,
               })
            );

            setOpenSignup(false);
            setUser("");
            setEmail("");
            setPass("");
            setSuccessMsg("Signed Up Seccessfully! Please Login.");
         })
         .catch((error) => {
            alert(error.message);
         });

      setPass("");
   };
   const Login = (e) => {
      e.preventDefault();

      auth
         .signInWithEmailAndPassword(email, pass)
         .then(() => {
            setOpenLogin(false);
         })
         .catch((err) => alert(err.message));

      setUser("");
      setEmail("");
      setPass("");
   };

   return (
      <div className="modalContainer">
         {currentUser.loggedIn ? (
            <Button
               className="modalContainer__button"
               variant="outlined"
               color="secondary"
               onClick={() => {
                  auth.signOut();
               }}
               endIcon={<ExitToAppOutlinedIcon />}
            >
               Log Out
            </Button>
         ) : (
            <div className="modalContainer__registerButton">
               <Button
                  className="registerButton__signup"
                  variant="contained"
                  color="primary"
                  onClick={() => setOpenSignup(true)}
               >
                  Sing Up
               </Button>
               <Button
                  className="registerButton__login"
                  variant="contained"
                  color="inherit"
                  onClick={() => setOpenLogin(true)}
               >
                  Log In
               </Button>
            </div>
         )}
         <Modal
            className="signup_modal"
            open={openSignup}
            onClose={() => setOpenSignup(false)}
         >
            <div style={modalStyle} className={classes.paper}>
               <div className="modalContainer__logo">
                  <img
                     src={process.env.PUBLIC_URL + "/Images/insta_logo.jpg"}
                     alt="Inta logo"
                     className="img-fluid logo_img"
                  />
               </div>
               <form className="modalContainer__form">
                  <div className="form__inputField">
                     <div className="inputField__userInput">
                        <Input
                           startAdornment={
                              <InputAdornment position="start">
                                 <AccountCircle />
                              </InputAdornment>
                           }
                           fullWidth={true}
                           placeholder="Username"
                           className="ml-2"
                           onChange={(e) => setUser(e.target.value)}
                           value={user}
                        />
                     </div>
                     <div className="inputField__emailInput">
                        <Input
                           startAdornment={
                              <InputAdornment position="start">
                                 <EmailIcon />
                              </InputAdornment>
                           }
                           fullWidth={true}
                           placeholder="Email"
                           className="ml-2"
                           type="email"
                           onChange={(e) => setEmail(e.target.value)}
                           value={email}
                        />
                     </div>
                     <div className="inputField__passInput">
                        <Input
                           startAdornment={
                              <InputAdornment
                                 variant="outlined"
                                 position="start"
                              >
                                 <VpnKeyIcon />
                              </InputAdornment>
                           }
                           fullWidth={true}
                           placeholder="Password"
                           className="ml-2"
                           type="password"
                           onChange={(e) => setPass(e.target.value)}
                           value={pass}
                        />
                     </div>
                  </div>
                  <div className="form__submitButton text-right mt-4">
                     <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        onClick={Signup}
                     >
                        Sign Up
                     </Button>
                  </div>
               </form>
            </div>
         </Modal>

         <Modal
            className="login_modal"
            open={openLogin}
            onClose={() => setOpenLogin(false)}
         >
            <div style={modalStyle} className={classes.paper}>
               <div className="modalContainer__logo">
                  <img
                     src={process.env.PUBLIC_URL + "/Images/insta_logo.jpg"}
                     alt="Inta logo"
                     className="img-fluid logo_img"
                  />
               </div>
               {successMsg ? (
                  <div className="signedUp_msg mt-1 text-center text-success">
                     <p>{successMsg}</p>
                  </div>
               ) : null}
               <form className="modalContainer__form">
                  <div className="form__inputField">
                     <div className="inputField__emailInput">
                        <Input
                           startAdornment={
                              <InputAdornment position="start">
                                 <EmailIcon />
                              </InputAdornment>
                           }
                           fullWidth={true}
                           placeholder="Email"
                           className="ml-2"
                           type="email"
                           onChange={(e) => setEmail(e.target.value)}
                           value={email}
                        />
                     </div>
                     <div className="inputField__passInput">
                        <Input
                           startAdornment={
                              <InputAdornment
                                 variant="outlined"
                                 position="start"
                              >
                                 <VpnKeyIcon />
                              </InputAdornment>
                           }
                           fullWidth={true}
                           placeholder="Password"
                           className="ml-2"
                           type="password"
                           onChange={(e) => setPass(e.target.value)}
                           value={pass}
                        />
                     </div>
                  </div>
                  <div className="form__submitButton text-right mt-4">
                     <Button
                        type="submit"
                        variant="contained"
                        color="inherit"
                        onClick={Login}
                     >
                        Log In
                     </Button>
                  </div>
               </form>
            </div>
         </Modal>
      </div>
   );
}
