import React, { useState, useEffect } from "react";
import "./Sass/main.css";
import Post from "./comps/Post";
import { db, auth } from "./Firebase/Firebase";
import Modal from "./comps/Modal_setup";
import ImgUpload from "./comps/ImgUpload";
import LoadingSpinner from "./comps/LoadingSpinner";

import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline";
import { Avatar } from "@material-ui/core";

import { useSelector } from "react-redux";

function App() {
   const { currentUser } = useSelector((state) => state);

   const [posts, setPosts] = useState([]);
   const [posts_loaded, setPosts_loaded] = useState(false);
   const [online, setOnline] = useState(null);

   useEffect(() => {
      db.collection("posts")
         .orderBy("timeStamp", "desc")
         .onSnapshot((snap) => {
            setPosts(
               snap.docs.map((doc) => {
                  return { id: doc.id, posts: doc.data() };
               })
            );
            setPosts_loaded(true);
         });

      setOnline(window.navigator.onLine);
   }, []);

   return (
      <div className="app container-fluid">
         <div className="app__header p-2">
            <h3 className="header__title">Post Media</h3>
            <Modal />
         </div>
         {currentUser.username !== "unknown" ? (
            <div className="app__currentUser container-fluid row">
               <div className="currentUser__allItems col-lg-4 col-md-6 col-sm-10">
                  <p className="mr-3">Logged in as </p>
                  <strong>{currentUser.username}</strong>
               </div>
            </div>
         ) : null}
         <div className="app__body row container-fluid">
            {online ? (
               <div className="app__posts col-10 col-md-10 col-lg-4">
                  {posts_loaded ? (
                     <div>
                        {posts.map((objects) => {
                           return (
                              <Post
                                 key={objects.id}
                                 username={objects.posts.username}
                                 imgUrl={objects.posts.imgUrl}
                                 caption={objects.posts.caption}
                                 postID={objects.id}
                              />
                           );
                        })}
                     </div>
                  ) : (
                     <div className="posts__loading-spinner">
                        <LoadingSpinner />
                     </div>
                  )}
               </div>
            ) : (
               <h1 className="app__body-offlineMsg mx-auto">
                  <ErrorOutlineIcon className="offlineMsg__icon" /> No Internet
                  Connection !!
               </h1>
            )}
         </div>
         {online ? (
            <div className="app__upload container-fluid">
               {currentUser.loggedIn ? (
                  <ImgUpload />
               ) : (
                  <div>
                     <h1>You need to login for Posting</h1>
                     <p className="upload__owener-name text-center">
                        Created By <strong>MD Omar</strong>
                     </p>
                  </div>
               )}
            </div>
         ) : null}
      </div>
   );
}

export default App;
